.collapsing + span,
.collapse.show + span {
  > .collapse-preview {
    display: none;
  }

  > .collapse-full {
    display: inline;
  }
}

.collapse + span {
  > .collapse-preview {
    display: inline;
  }

  > .collapse-full {
    display: none;
  }
}

.collapse-preview,
.collapse-full {
  .content {
    display: inline;
  }
}
